.form-container2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

form {
  display: flex;
  flex-direction: column;
  width: 300px;
  gap: 20px; /* Adds space between form elements */
}

h3 {
  color: #50985b;
  margin-bottom: 20px; /* Adds space below the heading */
}

form label {
  font-size: large;
  font-weight: bold;
  color: #2a2d44;
  margin-bottom: 5px; /* Ensures space between label and input/textarea */
}

form input, textarea {
  padding: 10px;
  width: 100%;
  height: var(--input-height);
  -webkit-padding-start: var(--input-padding);
  padding-inline-start: var(--input-padding);
  -webkit-padding-end: var(--input-padding);
  padding-inline-end: var(--input-padding);
  border-radius: var(--input-border-radius);
  outline: 2px solid transparent;
  outline-offset: 2px;
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition-property: var(--chakra-transition-property-common);
  transition-duration: var(--chakra-transition-duration-normal);
  --input-font-size: var(--chakra-fontSizes-md);
  --input-padding: var(--chakra-space-4);
  --input-border-radius: var(--chakra-radii-md);
  --input-height: var(--chakra-sizes-10);
  border: 1px solid;
  border-radius: 5px;
  margin-bottom: 5px; /* Adds space below each input/textarea */
}

form button {
  padding: 10px;
  font-size: 16px;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s; /* Smooth transition for hover effect */
}

form button:hover {
  background-color: #34495e;
}